import React, { useState } from "react";
import "./FormGen.css";

const FormGen = ({ actualizarDatos, handleCompartir, linkGenerado }) => {  
  if (!actualizarDatos) {
    console.error("Error: La funcin `actualizarDatos` no fue pasada correctamente.");
  }

  const [nombre, setNombre] = useState("");
  const [invitadosAdultos, setInvitadosAdultos] = useState(1);
  const [invitadosNios, setInvitadosNios] = useState(0);
  const [incluirNios, setIncluirNios] = useState(false);
  const [nombreCliente, setNombreCliente] = useState("");  


  const manejarCambioNombre = (e) => {
    let valor = e.target.value.replace(/\s{2,}/g, " "); 

    if (!/^[a-zA-Z\s.,\-\/]*$/.test(valor)) {
      alert("Solo puedes introducir texto y los caracteres especiales: . , - /");
      return;
    }

    if (valor.length > 28) {
      alert("El nombre no puede tener ms de 24 caracteres.");
      return;
    }

    setNombreCliente(valor); // <--- importante
  };


  const manejarCambioInvitadosAdultos = (e) => {
    let valor = e.target.value;

    if (valor === "") {
      setInvitadosAdultos("");
      return;
    }

    let numeroInvitados = parseInt(valor, 10);
    if (isNaN(numeroInvitados) || numeroInvitados < 1 || numeroInvitados > 25) {
      alert("El nmero de invitados adultos debe estar entre 1 y 25.");
      return;
    }

    setInvitadosAdultos(numeroInvitados);
  };

  //  Correccin: Definir `manejarCambioInvitadosNios`
  const manejarCambioInvitadosNios = (e) => {
    let valor = e.target.value;
  
    if (valor === "") {
      setInvitadosNios("");
      return;
    }
  
    let numeroInvitados = parseInt(valor, 10);
    if (isNaN(numeroInvitados) || numeroInvitados < 1 || numeroInvitados > 25) {
      alert("El nmero de invitados adultos debe estar entre 1 y 25.");
      return;
    }
  
    setInvitadosNios(numeroInvitados);
  };

  const manejarCheckbox = () => {
    setIncluirNios(!incluirNios);
    if (!incluirNios) {
      setInvitadosNios(prev => prev > 0 ? prev : 1);
    } else {
      setInvitadosNios(0); //  Si se desactiva, el nmero de nios se borra
    }
  };

  const manejarEnvio = (e) => {
    e.preventDefault();
    actualizarDatos(nombreCliente, nombre, invitadosAdultos, invitadosNios, incluirNios);
  };

  return (
    <section id="asistencia">
      <div className="card">
        <h1>Genera tu Invitacin</h1>
        <form onSubmit={manejarEnvio}>
          <label className="text">Nombre del Invitado:</label>
          <input type="text" value={nombreCliente} onChange={manejarCambioNombre} required/>

          <label className="text">Nmero de Invitados (Adultos):</label>
          <input type="number" value={invitadosAdultos} min="1" max="25" onChange={manejarCambioInvitadosAdultos} required />

          <label>
            <input type="checkbox" checked={incluirNios} onChange={manejarCheckbox} /> Incluir nios?
          </label>

          {/*  Solo muestra el campo si `incluirNios` est activado */}
          {incluirNios && (
            <>
              <label className="text">Nmero de Invitados (Nios):</label>
              <input type="number" value={invitadosNios} min="0" max="25" onChange={manejarCambioInvitadosNios} required />
            </>
          )}

          <button type="submit">Generar Invitacin</button>

          {linkGenerado && (
            <button type="button" onClick={handleCompartir}> Compartir Invitacin</button>
          )}
        </form>
      </div>
    </section>
  );
};

export default FormGen;
